<template>
  <div class="zero">
    <!-- 顶部图片 -->
    <img
      class="zero__img-auto"
      :src="require('@/assets/zeroLive/topBg199.jpg')" />
    <!-- 报名按钮 -->
    <div class="part6">
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请输入手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix"> +86 <span class="icon">▼</span> </span>
          </template>
        </VanField>
        <VanField
          v-model="form.code"
          name="code"
          placeholder="请输入验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <div class="subBtn" @click="submit">199元抢购</div>
      </VanForm>
    </div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>报名成功</p>
          <p>{{ el.time }}秒前</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <!-- 底部图片 -->
    <img
      class="zero__img-auto"
      v-for="(item, index) in detailImg"
      :key="index"
      :src="require(`@/assets/zeroLive/${item}.png`)" />
    <!-- 底部按钮 -->
    <div class="zero__footer">
      <div class="zero__footer__topic">
        <span class="zero__footer__topic__title">市场价:<span class="price">199</span>元</span>
      </div>
      <div class="zero__footer__btn" @click="submit"><span>立即报名</span></div>
    </div>
  </div>
</template>

<script>
import { Swipe, SwipeItem } from 'vant'
import { codeMap } from './ErrorMsg'
import { getSearch, calArray } from '@/utils'
import CountDown from '@/components/common/CountDown'
import wxAuth from '@/helper/Auth/wxAuth'
import { mapState } from 'vuex'

export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem,
    CountDown
  },
  data () {
    return {
      detailImg: [1, 2, 3, 4, '6c'],
      swipeList: [], // 轮播数组
      paytype: 1, // 支付类型
      form: {} // 表单
    }
  },
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({
      getTokenQuery: { ...getSearch() }
    })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  async created () {
    // const {
    //   id,
    //   unionid,
    //   wx_body: { openid }
    // } = this.userInfo
    // this.$log.update({
    //   openid,
    //   unionid,
    //   uid: id,
    //   mid: id
    // })
    this.checkOrderInWx()
    this.getRecentBuy()
  },
  methods: {
    // 获取最新报名客户
    async getRecentBuy () {
      const res = await this.$apis.userOperator.getLatestUser()
      this.swipeList = calArray(res, 2)
    },
    submit () {
      // this.$log.click({
      //   current_page: location.href,
      //   module_ori: '表单提交',
      //   module_part: '106_toufang_lcx_0245'
      // })
      this.$apis.userOperator.report()
      this.$refs.form.submit()
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('ZeroLiveInWx199-phone', this.form.phone)
      localStorage.setItem('ZeroLiveInWx199-code', this.form.code)
      this.pay()
    },
    // 获取验证码
    beforeSend (start) {
      // this.$log.click({
      //   current_page: location.href,
      //   module_ori: '表单提交',
      //   module_part: '106_toufang_lcx_0136'
      // })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30009) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30009) {
          this.goSucccess()
        }
        // this.$log.click(codeMap[err.code].report)
      })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    checkOrderInWx () {
      return this.$apis.payment
        .checkOrderInWx({ product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE' })
        .catch((err) => {
          this.payErrorBack(err)
          return Promise.reject(err)
        })
    },
    payErrorBack (data) {
      if (data.code === 30009) {
        this.$dialog({
          message: '您已学习过此课程',
          confirmButtonText: '查看老师'
        }).then(() => {
          // this.$log.click(codeMap[data.code].report)
          this.$router.push({
            name: 'QrcodeInWx',
            query: {
              productSn: data.data.product_sn
            }
          })
        })
        return
      }
      if (codeMap[data.code]) {
        this.$dialog({ message: data.msg || codeMap[data.code].msg }).then(() => {
          // this.$log.click(codeMap[data.code].report)
        })
      }
    },
    async pay () {
      // this.$log.click({
      //   current_page: location.href,
      //   module_ori: '表单提交',
      //   module_part: '106_toufang_lcx_0386'
      // })
      this.$apis.payment.payInWx({
        pay_type: 2,
        pay_mode: 3,
        ...getSearch(),
        ...this.form,
        product_sn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE'
      })
        .then(result => {
          window.WeixinJSBridge.invoke('getBrandWCPayRequest', result, (res) => {
            if (res.err_msg === 'get_brand_wcpay_request:ok') {
              this.$router.push({
                name: 'QrcodeInWx',
                query: {
                  productSn: 'PRODUCT_SN_XIAOBAI_199_MH_LIVE'
                }
              })
            }
          })
        })
        .catch(err => {
          this.payErrorBack(err)
        })
    }
  }
}
</script>
