<!--倒计时组件-->
<template>
  <VanCountDown
    ref="countDown"
    class="button"
    millisecond
    :time="60000"
    :time-data="'timeData'"
    :auto-start="false"
    format="ss"
    @finish="finish">
    <template #default="timeData">
      <span
        class="block"
        style="text-align:center;"
        @click="start">{{ !timeData.milliseconds? text:timeData.seconds + "s" }}</span>
    </template></VanCountDown>
</template>

<script>
export default {
  props: {
    // 发送验证码
    beforeSend: {
      type: Function,
      default: () => {}
    },
    text: {
      type: String,
      default: '获取验证码'
    }
  },
  data: function () {
    return {
      isFinish: true
    }
  },
  methods: {
    finish (e) {
      this.isFinish = true
      this.$refs.countDown.reset()
    },
    // 获取验证码
    start () {
      if (this.$refs.countDown.timeData.milliseconds) return
      this.isFinish = false
      this.beforeSend(this.$refs.countDown.start)
    }
  }

}
</script>

<style lang="scss" scoped>
.button{
  color: inherit !important;
  font-size: inherit !important;
}
</style>
